import React from "react";
import { UploadOutlined } from "@ant-design/icons";
import styles from "./styles.module.scss";
import {
  Button,
  Form,
  Input,
  InputNumber,
  message,
  Rate,
  Select,
  Space,
  Upload,
} from "antd";
import useProductStore from "../../store/store";
import GoBackButton from "../../components/common/GoBackButton";
import { Product } from "../../types/types";

const { Option } = Select;
const { TextArea } = Input;

// 
const validationRules = {
  title: [
    { required: true, message: ",   !" },
    {
      min: 3,
      max: 50,
      message:
        "  .  3 ,  - 50!",
    },
  ],
  description: [
    { required: true, message: ",   !" },
    {
      max: 500,
      message: "   !  500 !",
    },
  ],
  price: [{ required: true, message: ",  !" }],
  category: [{ required: true, message: ",  !" }],
};

const formItemLayout = {
  labelCol: { span: 6 },
  wrapperCol: { span: 14 },
};

const normFile = (e: any) => e?.fileList || [];

const CreateProduct: React.FC = () => {
  const [form] = Form.useForm(); //   
  const addNewProduct = useProductStore((state) => state.addProduct);
  const [messageApi, contextHolder] = message.useMessage();

  const successMessage = () => {
    messageApi.success("   !", 3);
  };

  const onFinish = (
    values: Omit<Product, "id" | "rating"> & {
      rate: number;
      upload: Array<Object>;
    },
  ) => {
    const newProduct = {
      title: values.title,
      price: values.price,
      description: values.description,
      category: values.category,
      image:
        "https://vsesvoi43.ru/wp-content/uploads/2022/06/prava-potrebitelej-budut-luchshe-zashhishheny.png",
      rating: {
        rate: values.rate,
        count: 0,
      },
    };

    addNewProduct(newProduct);
    form.resetFields();
    successMessage();
  };

  return (
    <>
      <GoBackButton />
      <Form
        form={form}
        {...formItemLayout}
        onFinish={onFinish}
        className={styles.createForm}
      >
        <Form.Item
          name="title"
          label=""
          rules={validationRules.title}
        >
          <Input />
        </Form.Item>

        <Form.Item
          name="description"
          label=""
          rules={validationRules.description}
        >
          <TextArea autoSize={{ minRows: 3, maxRows: 5 }} />
        </Form.Item>

        <Form.Item name="price" label="" rules={validationRules.price}>
          <InputNumber min={0.01} max={10000000} />
        </Form.Item>

        <Form.Item
          name="category"
          label=""
          hasFeedback
          rules={validationRules.category}
        >
          <Select placeholder=",  ">
            <Option value="men's clothing"> </Option>
            <Option value="jewelery"></Option>
            <Option value="electronics"></Option>
            <Option value="women's clothing"> </Option>
          </Select>
        </Form.Item>

        <Form.Item name="rate" label="">
          <Rate />
        </Form.Item>

        <Form.Item
          name="upload"
          label=""
          valuePropName="fileList"
          getValueFromEvent={normFile}
        >
          <Upload name="image" listType="picture" maxCount={1}>
            <Button icon={<UploadOutlined />}>  </Button>
          </Upload>
        </Form.Item>

        <Form.Item wrapperCol={{ span: 12, offset: 6 }}>
          <Space>
            {contextHolder}
            <Button type="primary" htmlType="submit">
              
            </Button>
            <Button htmlType="reset"></Button>
          </Space>
        </Form.Item>
      </Form>
    </>
  );
};

export default CreateProduct;
